@use 'sass:color'
@use 'header'

$textColor: #040103
$primaryColor: #bfe5bc
$secondaryColor: #83c181
$thirdColor: #509051
    
@media (prefers-color-scheme: dark) 
    $primaryColor: darken($primaryColor, 50%)
    $secondaryColor: darken($primaryColor, 50%)
    $thirdColor: darken($primaryColor, 50%)

@mixin hoverHighlight 
    &:hover 
        background-color: color.scale($primaryColor, $lightness: -10%, $saturation: 10%)
        border: solid 1px black

*
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif
    color: $textColor

header
    background-color: $secondaryColor

    #top
        display: flex

        div
            margin: auto
            margin-left: 2vmax

            h3
                margin: 0
                font-size: 3.5vmin

            h1
                font-size: 6vmin
                margin: 0

        picture
            max-width: 40%

            img
                max-width: 100%

    nav
        display: flex
        justify-content: space-around
        border: solid 1px $thirdColor
        background-color: $primaryColor

        div
            position: relative

            button
                background-color: $primaryColor
                padding: 1vmin
                border: none

            div
                position: absolute
                z-index: 1
                display: none
                background-color: $primaryColor
                border: 1px $thirdColor solid
                border-radius: 10%
                right: -25%

            &:hover div
                display: block


h2, button
    font-size: 4vmin
    text-decoration: underline
    padding: 2vmin
    border-radius: 2vmin
    @include hoverHighlight
    margin: 0

    